@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --color-text: #212121;
  --color-primary: #007ac6;
  --color-btn: #267bc0;
  --color-grey: #4a4a4a;
  --color-lightgrey: #f7f7f7;
  --color-border: rgba(0, 0, 0, 0.1);
  --color-success: #417505;
  --color-danger: #d0021b;
  --color-blue-bg: #f7f9fd;
}

svg {
  vertical-align: middle;
}

/* Utilities */

.position-relative {
  position: relative;
}

.mb-0 {
  margin-bottom: 0;
}

.mb-1 {
  margin-bottom: 0.25rem;
}

.mb-2 {
  margin-bottom: 0.5rem;
}

.mb-3 {
  margin-bottom: 1rem;
}

.mb-4 {
  margin-bottom: 2rem;
}

.mr-1 {
  margin-right: 0.25rem;
}

.mr-2 {
  margin-right: 0.5rem;
}

.mr-3 {
  margin-right: 1rem;
}

.ml-1 {
  margin-left: 0.25rem;
}

.ml-2 {
  margin-left: 0.5rem;
}

.ml-3 {
  margin-left: 1rem;
}

.lg {
  font-size: 115%;
}

.text-success {
  color: #417505;
  color: var(--color-success);
}

.text-danger {
  color: #d0021b;
  color: var(--color-danger);
}

.text-grey {
  color: #4a4a4a;
  color: var(--color-grey);
}

.text-center {
  text-align: center;
}

.text-uppercase {
  text-transform: uppercase !important;
}

.text-capitalize {
  text-transform: capitalize !important;
}

.font-weight-semibold {
  font-weight: 600;
}

.font-weight-bold {
  font-weight: 700;
}

.d-flex {
  display: -webkit-flex;
  display: flex;
}

.align-items-center {
  -webkit-align-items: center;
          align-items: center;
}

.justify-content-between {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 26px;
}

/* Common Styles */

a,
button {
  cursor: pointer;
}

a.inline-link {
  color: #0079cc;
  font-weight: 600;
}

body {
  font-family: "Open Sans";
}

.campaigns-page {
  /* font-family: "Open Sans"; */
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 0;
  color: #212121;
  color: var(--color-text);
}

.campaigns-page .container {
  padding: 0 32px;
}

.input-group {
  margin-bottom: 6px;
}

.input-group input,
.input-group label {
  cursor: pointer;
}

input[type="checkbox"] {
  margin-right: 6px;
}

input[type="checkbox"]:checked ~ label {
  font-weight: 600;
}

input[type="text"] {
  height: 100%;
  border: 1px solid #ccc;
  border-radius: 3px;
}

/* Button */

.ant-btn {
  border: none;
  outline: none;
  height: auto;
  padding: 5px 15px;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  border-radius: 3px;
  background: #fff;
  border: 1px solid #267bc0;
  border: 1px solid var(--color-btn);
  color: #267bc0;
  color: var(--color-btn);
}

.ant-btn.ant-btn-primary {
  background: #267bc0;
  background: var(--color-btn);
  color: #fff;
}

.ant-btn[disabled],
.ant-btn[disabled]:hover,
.ant-btn[disabled]:focus,
.ant-btn[disabled]:active {
  color: #c9c9c9;
  background: #ffffff;
  border-color: #c9c9c9;
}

.ant-btn-primary[disabled],
.ant-btn-primary[disabled]:hover,
.ant-btn-primary[disabled]:focus,
.ant-btn-primary[disabled]:active {
  color: #ffffff;
  background: #c9c9c9;
  border-color: #c9c9c9;
}

.ant-btn svg {
  font-size: 16px;
  margin-right: 4px;
  margin-bottom: 2px;
}

.round-button {
  display: inline-block;
  padding: 4px 16px;
  font-size: 14px;
  font-weight: 600;
  border: 1px solid #007ac6;
  border: 1px solid var(--color-primary);
  border-radius: 15px;
  background: #fff;
}

/* Link */

.btn-link {
  text-decoration: none;
  font-family: "Open Sans";
  font-size: 14px;
  color: #007ac6;
  color: var(--color-primary);
  cursor: pointer;
}

.btn-link.lg {
  font-size: 16px;
  font-weight: 600;
}

/* Header */

.campaigns-page__header {
  margin: 16px 0 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.campaigns-page__header--left {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

h1.page-title {
  font-size: 18px;
  font-weight: 700;
  margin-right: 24px;
}

.pagetabs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.pagetabs-item {
  position: relative;
  padding: 7px 16px;
  margin-right: 2px;
  background: #999;
  font-size: 14px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.pagetabs-item.active {
  background: #007ac6;
  background: var(--color-primary);
}

.pagetabs-item a {
  color: #fff;
}

/* Nav - Tabs */

#campaignsNav {
  border-top: 1px solid rgba(0, 0, 0, 0.15);
}

.tabs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.tab-item {
  position: relative;
  padding: 20px 15px;
  font-size: 14px;
  color: var(--text);
  opacity: 0.5;
  cursor: pointer;
}

.tab-item.active {
  color: #007ac6;
  color: var(--color-primary);
  font-weight: 600;
  opacity: 1;
}

.tab-item.active:after {
  content: "";
  width: 100%;
  height: 3px;
  background: #267bc0;
  background: var(--color-btn);
  position: absolute;
  bottom: 0;
  left: 0;
}

/* Filter */

.container.campaigns-page__filter-section {
  padding: 16px 32px;
  background: #fafafa;
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

/* Table */

.campaings-page__table-container {
  padding: 20px 32px;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

thead {
  background: #f7f7f7;
  background: var(--color-lightgrey);
  border: none;
}

thead {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border: 1px solid var(--color-border);
}

thead td a {
  color: inherit;
  cursor: pointer;
}

thead td a:hover {
  color: inherit;
}

a[data-sortable] {
  display: -webkit-flex;
  display: flex;
}

.sort-icon {
  width: 14px;
  margin-left: 4px;
}

tbody tr {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border: 1px solid var(--color-border);
}

tbody tr:hover {
  background: #f7f9fd;
  background: var(--color-blue-bg);
}

thead td {
  border: none;
  padding: 12px 20px;
  background: #f7f7f7;
  background: var(--color-lightgrey);
}

tbody td {
  padding: 17px 20px;
}

.performance-icon svg {
  vertical-align: middle;
  margin-bottom: 3px;
  margin-right: 3px;
}

.table-actions__trigger {
  position: relative;
  display: grid;
  place-items: center;
  cursor: pointer;
  color: inherit;
}

.table-actions__trigger:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: #2b8ccf12;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.table-actions__trigger:hover,
.table-actions__trigger:focus {
  color: #007ac6;
  color: var(--color-primary);
}

.table-actions__trigger:hover:before,
.table-actions__trigger:focus:before {
  opacity: 1;
}

a.table-actions__menu-item {
  margin: 1px 0;
  padding: 1px 0;
  font-family: "Open Sans";
  font-size: 15px;
  font-weight: 600;
  color: #007ac6;
  color: var(--color-primary);
}

.table-actions__menu-item svg {
  font-size: 22px;
  margin-bottom: 2px;
  margin-right: 8px;
}

.ant-dropdown-menu-item:hover {
  background-color: #f7f9fd;
  background-color: var(--color-blue-bg);
  color: #007ac6;
  color: var(--color-primary);
}

a.table-actions__menu-item:hover {
  background-color: #f7f9fd;
  background-color: var(--color-blue-bg);
  color: #007ac6;
  color: var(--color-primary);
}

.ant-dropdown-placement-bottomRight > .ant-dropdown-arrow {
  right: 6px;
}

.ant-dropdown-menu-item {
  padding-right: calc(12px + 10px);
}

.page-count {
  margin-top: 8px;
  text-align: right;
}

/* Table Filters */

.table-filters {
  position: relative;
}

.table-filters__trigger {
  color: #007ac6;
  color: var(--color-primary);
  font-weight: 600;
  cursor: pointer;
}

.table-filters__dropdown {
  width: 320px;
  position: absolute;
  left: 40px;
  top: calc(100% + 10px);
  background: #fff;
  border: 1px solid #cbcbcb;
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translateY(-10px) scaleY(0.5);
          transform: translateY(-10px) scaleY(0.5);
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
  transition: 0.15s ease;
}

.table-filters__dropdown.show {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateY(0) scaleY(1);
          transform: translateY(0) scaleY(1);
}

.table-filters__dropdown:after {
  content: "";
  width: 10px;
  height: 10px;
  display: block;
  position: absolute;
  top: -6px;
  left: calc(80px + 10px);
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background: #fff;
  border-left: 1px solid #ccc;
  border-top: 1px solid #ccc;
}

.table-filters__dropdown-filters {
  padding: 16px;
}

.table-filters__dropdown-footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 8px 16px;
  border-top: 1px solid #cbcbcb;
}

/* Table Search */

.campaigns-table__search {
  position: relative;
}

.campaigns-table__search input {
  width: 240px;
  height: 34px;
  margin-right: 24px;
  padding-left: 38px;
}

.campaigns-table__search input::-webkit-input-placeholder {
  color: #ccc;
}

.campaigns-table__search input::placeholder {
  color: #ccc;
}

.campaigns-table__search svg {
  font-size: 24px;
  position: absolute;
  top: 50%;
  left: 9px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

/* Modal */

.ant-modal-title {
  font-weight: 600;
}

.create-modal .ant-modal-title {
  font-size: 20px;
  font-weight: 400;
  padding: 10px 0;
}

.ant-modal-footer {
  padding: 16px;
  border-top: none;
}

.ant-modal-footer .ant-btn + .ant-btn:not(.ant-dropdown-trigger) {
  margin-bottom: 0;
  margin-left: 16px;
}

/* Form */

.form-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 32px;
  grid-row-gap: 24px;
}

.span-2 {
  grid-column: span 2;
}

.span-4 {
  grid-column: span 4;
}

.ant-form-item {
  margin-bottom: 0;
}

.ant-form-item-label > label {
  font-size: 13px;
  color: rgba(64, 64, 64, 0.75);
}

.ant-form-vertical .ant-form-item-label {
  padding: 0 0 4px;
}

.ant-input {
  padding: 6px 11px;
}

.ant-picker,
.ant-input-number {
  width: 100%;
}

/* Campaign Summary Page */

.campaign-summary__container.container {
  padding: 32px;
}

.campaign-summary__header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 24px;
}

.campaign-summary__header h1 {
  font-size: 27px;
  margin-bottom: 12px;
}

/* offset to align with campaign name */
.campaign-summary__back,
.campaign-summary__actions {
  margin-top: 8px;
}

.status-badge {
  padding: 4px 12px;
  font-size: 12px;
  color: #fff;
  border-radius: 4px;
}

.status-badge-scheduled {
  background: #13b3c6;
}

.status-badge-in-progress {
  background: #34b94a;
}

.status-badge-paused {
  background: #d87d28;
}

.status-badge-completed {
  background: #057137;
}

.status-badge-archived {
  background: #5b687b;
}

.status-badge-failed {
  background: #d0021b;
  background: var(--color-danger);
}

.campaign-summary__actions button:not(:last-child) {
  margin-right: 20px;
}

.campaign-summary__details,
.campaign-summary__stats {
  min-height: 420px;
  height: 65vh;
  overflow-y: scroll;
  border: 1px solid #dfdfdf;
  border-radius: 2px;
}

.campaign-summary__details {
  padding: 24px;
}

.campaign-summary__stats-header {
  padding: 16px 20px;
  border-bottom: 1px solid #dfdfdf;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.campaign-summary__details h4 {
  font-weight: 600;
  margin-bottom: 2px;
}

.campaign-summary__details p {
  margin-bottom: 0;
}

.campaign-summary__stats-chart {
  padding: 24px;
  display: grid;
  place-items: center;
}

.chart-grid {
  width: 100%;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.chart-container {
  position: relative;
  margin: 24px 0;
  padding: 12px;
}

.chart-total-numbers {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.chart-total-numbers p {
  font-size: 13px;
}

.chart-legend-item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
}

.chart-legend-color {
  width: 12px;
  height: 12px;
  margin-right: 7px;
}

.chart-legend-name {
  min-width: 100px;
}

.chart-legend-value {
  font-weight: 600;
}

/* Call Details */

.call-details-trigger {
  position: relative;
  padding: 24px 0;
  display: grid;
  place-items: center;
}

.call-details-trigger svg {
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.call-details-trigger svg.rotated {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.call-details-trigger:before {
  z-index: -1;
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.call-details-trigger a:before {
  z-index: -1;
  content: "";
  display: block;
  width: 115%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: #fff;
}

.call-details-container {
  max-height: 0;
  overflow: hidden;
  transition: 0.15s ease;
}

.call-details-container.show {
  max-height: 1200px;
  transition: 0.5s ease;
}

#callDetailsTable td {
  padding: 12px 16px;
}

#callDetailsTable audio {
  width: 100%;
  height: 36px;
}

.call-details-dropdown .ant-dropdown-menu {
  padding: 16px;
}

.call-details-dropdown h4 {
  margin-bottom: 16px;
}

svg.copy-sid {
  color: #007ac6;
  color: var(--color-primary);
  font-size: 18px;
  cursor: pointer;
}

.list-actions-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.list-actions-item {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-align-items: center;
          align-items: center;
  color: #007ac6;
  color: var(--color-primary);
}

.list-actions-item svg {
  margin-right: 4px;
  stroke-width: 2.5px;
}

.list-actions-item:not(:first-child) {
  margin-left: 22px;
  padding: 0 5px;
}

.list-actions-item.list-actions-item__delete {
  color: rgba(0, 0, 0, 0.4);
}

.list-actions-item.list-actions-item__delete:hover {
  color: #d0021b;
  color: var(--color-danger);
}

/* Create List Modal + Form */

.list-modal .ant-modal {
  max-width: 450px;
}

.ant-upload.ant-upload-drag {
  padding: 16px;
  border: 2px dashed #d9d9d9;
}

.ant-upload-hint {
  line-height: 180%;
}

.sample-csv {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 13px;
}

